<template lang='pug'>
  #app
    header
      .jumbotron.text-center
        h1 Regalia Crafting Sim
        p.text-muted Alpha (free) hosted by 
          u Surge.sh
    .img-div.text-center
      img(src='./assets/body-vaalRegalia.png' alt='Crafting item image')
      p ilvl: {{ itemLevel }}
    .orbs.text-center
      img(src='./assets/transmute-orb.png' alt='transmutation orb' @click='useTransmute')
      img(src='./assets/alteration-orb.png' alt='alteration orb' @click='useAlt')
      img(src='./assets/augment-orb.png' alt='augmentation orb' @click='useAug')
      img(src='./assets/scouring-orb.png' alt='scouring orb' @click='useScour')
    .mods(v-for='n in mods').text-center
      //- p 
      //-   b {{ mod.prefix }} 
      //-   | +{{ mod.range[0] }} to {{ mod.range[1] }} {{ mod.mod }}
      p 
        | t{{ n.tier.tier }} 
        b {{ n.tier.prefix }} 
        b {{ n.tier.suffix }} 
        | +{{ n.tier.range[0] }} to {{ n.tier.range[1] }} {{ n.mod }}
        
      

  
</template>

<script>
import Hello from './components/Hello';
import Mods from './mods.js';
var _ = require('lodash');



export default {
  name: 'app',
  data() {
    return {
      itemLevel: 84 ,
      modCount: 0,
      mods: []
    }
  },
  methods: {
  
    // Transmutation Orb
    useTransmute() {
      if (this.modCount === 0) {
        var nOfMods = _.random(1, 2);
        var modsArray = [];
        if (nOfMods == 1) {
          var selectedAffix = _.sample(mods.esBody);
          var selectedMod = _.sample(selectedAffix);
          var mod = selectedMod.mod;
          var tier = _.sample(selectedMod.tiers);
          modsArray.push(
            {
              mod: mod,
              tier: tier,
              affix: selectedMod.affix
            }
          );
        this.modCount = 1;
          
        } else {
          var selectedPrefix = _.sample(mods.esBody.prefix);
          var selectedSuffix = _.sample(mods.esBody.suffix);
          modsArray.push(
            {
              mod: selectedPrefix.mod,
              tier: _.sample(selectedPrefix.tiers),
              affix: selectedPrefix.affix
            },
            {
              mod: selectedSuffix.mod,
              tier: _.sample(selectedSuffix.tiers),
              affix: selectedSuffix.affix
            }
          )
          this.modCount = 2;
        }
        this.mods = modsArray;
      }
    },
    
    // Alteration 
    useAlt() {
      if (this.modCount > 0 && this.modCount < 3) {
        var nOfMods = _.random(1, 2);
        var modsArray = [];
        if (nOfMods == 1) {
          var selectedAffix = _.sample(mods.esBody);
          var selectedMod = _.sample(selectedAffix);
          var mod = selectedMod.mod;
          var tier = _.sample(selectedMod.tiers);
          var affix = selectedMod.affix;
          modsArray.push(
            {
              mod: mod,
              tier: tier,
              affix: affix
            }
          );
        this.modCount = 1;
          
        } else {
          var selectedPrefix = _.sample(mods.esBody.prefix);
          var selectedSuffix = _.sample(mods.esBody.suffix);
          modsArray.push(
            {
              mod: selectedPrefix.mod,
              tier: _.sample(selectedPrefix.tiers),
              affix: selectedPrefix.affix
            },
            {
              mod: selectedSuffix.mod,
              tier: _.sample(selectedSuffix.tiers),
              affix: selectedSuffix.affix
              
            }
          )
          this.modCount = 2;
        }
        this.mods = modsArray;
      }
    },
    
    // Augmentation 
    useAug() {
      if (this.modCount == 1) {
        if (this.mods[0].affix == 'prefix') {
          var selectedSuffix = _.sample(mods.esBody.suffix);
          this.mods.push(
            {
              mod: selectedSuffix.mod,
              tier: _.sample(selectedSuffix.tiers),
              affix: selectedSuffix.affix
            }
          )
        } else {
          var selectedPrefix = _.sample(mods.esBody.prefix);
          this.mods.push(
            {
              mod: selectedPrefix.mod,
              tier: _.sample(selectedPrefix.tiers),
              affix: selectedPrefix.affix
            }
          )
        }
        this.modCount = 2;
      }
    },
    
    // Scouring Orb
    useScour() {
      this.modCount = 0;
      this.mods = [];
    }
  }
}
</script>

<style lang='scss'>
  
  body {
    padding: 20px;
  }
  
  .orbs {
    img {
      width: 48px;
      margin: 15px;
    }
  }
</style>



